function proxy(vm, target,key){
  // å°†dataä¸­çš„å±æ€§ä»£ç†ä¸€ä»½æ”¾åˆ°vmä¸‹ ==== 9
  Object.defineProperty(vm,key,{
    get(){
      return vm[target][key];
    },
    set(newValue){
      vm[target][key] = newValue;
    }
  })
}

function initData(vm) {  // å¤„ç†dataæ•°æ® ==== 6
  // å¾—åˆ°dataé€‰é¡¹
  let data = vm.$options.data;
  // åˆ¤æ–­dataæ˜¯å¦ä¸ºæ–¹æ³•ï¼ˆå¦‚æœæ˜¯dataé€‰é¡¹æ˜¯ä¸ªæ–¹æ³• å°±è°ƒç”¨æ­¤æ–¹æ³•å¹¶æ”¹å˜thisæŒ‡å‘ä¸º å½“å‰vueå®ä¾‹ï¼‰ ==== 7
  data = typeof data === "function" ? data.call(vm) : data;
  // console.log(data);

  vm._data = data;

  // æ•°æ®åŠ«æŒ ==== 8  -ã€‹./observe/index.js
  observe(data);

  
  
  //============== æ­¤éƒ¨åˆ†å…ˆä¸çœ‹ æ ¹æ®åºå·å¼•å¯¼æ¥çœ‹ 
  // ä¸ºäº†æ–¹ä¾¿æ˜¯vmç›´æ¥è·å–_dataä¸­çš„å±æ€§ å†æ¬¡ä»£ç†
  /**
   * data(){
   *   return {
   *      test:1
   *    }
   * }
   * this.test æˆ–è€…  vm.test
   */
  for (let key in data) {
    proxy(vm, "_data", key);
  }
  //=====
}
